.shopon_bio .active .animated_btn_bg{
    width: 46%;
}
.scale_preview_wrap{    
    max-width: 270px;
    height: 554px;
    width: 100%;
    overflow: hidden;
    border: 14px solid var(--bs-gray-400);
    border-radius: 40px;
    transition: all .3s ease;
}

.scale_preview{
    padding-bottom: 205%;
    border-radius: 22px;
    overflow: hidden;
}
.scale_preview iframe{
    position: absolute;
    top: -14.6%;
    left: -22%;
    width: 145%;
    height: 135%;
    transform: scale(0.7);
    border: 0;
    margin: 0;
    border-radius: 35px;
    border-top-left-radius: 0;
    border-top-right-radius: 0;
}

.laptop_active.scale_preview_wrap{
    max-width: 800px;
}


.laptop_active .scale_preview {
    padding-bottom: 68.1%;
}

.laptop_active .scale_preview iframe{
    top: -21.4%;
    left: -22%;
    width: 145%;
    height: 143%;
}

@media (max-width: 1600px){
.scale_preview_wrap {
    max-width: 265px;
    height: 540px;
}

.laptop_active.scale_preview_wrap {
    max-width: 680px;
}

.scale_preview_wrap{
    border: 12px solid var(--bs-gray-400);
}
.laptop_active .scale_preview {
    padding-bottom: 79%;
}

}